                  <path d="M14.1199 3.37331C20.044 3.37331 24.8665 8.19588 24.8665 14.12C24.8665 20.0441 20.044 24.8666 14.1199 24.8666C8.19576 24.8666 3.37319 20.0441 3.37319 14.12C3.37319 8.19588 8.19576 3.37331 14.1199 3.37331ZM14.1199 0.686646C6.70466 0.686646 0.686523 6.70478 0.686523 14.12C0.686523 21.5352 6.70466 27.5533 14.1199 27.5533C21.5351 27.5533 27.5532 21.5352 27.5532 14.12C27.5532 6.70478 21.5351 0.686646 14.1199 0.686646ZM14.1199 18.15L8.74652 12.7766H19.4932L14.1199 18.15Z" />
